<template lang="pug">
.group__scholars
    .group__scholars-title Список учеников группы
    ul.group__scholars-list 
        li.group__scholars-item(v-for='scholar,idx in groupScholars')
            scholar-item(
                :index='idx+1'
                :scholar='scholar'
                v-on:eventRemove='eventRemove'
            )
    button(@click='removeItems()').remove__scholars Удалить из группы
</template> 
<script>
import { mapActions, mapState } from "vuex";
export default {  
    data() {
        return {
            selectedIds: [this.group.id],
        }
    },
    created() {
        this.fetchGroupScholars(this.group.id)
    },
    components: {
        scholarItem: () => import("../../admin/groups/scholar-item"),
    },
    computed: {
        ...mapState ("groupScholars", {
            groupScholars: state => state.groupScholars}),
    },
    props: {
        group: Object
    },
    methods: {
        ...mapActions("groupScholars", ["removeScholarsGroup", "fetchGroupScholars"]),
        eventRemove(id) {
            if(id<0) this.selectedIds=this.selectedIds.filter(element => element!=-id)
            else {
                if(this.selectedIds && this.selectedIds.filter(element => element==id).length==0){
                    this.selectedIds.push(id)
                }
            }
        },
        removeItems() {
            if(this.selectedIds.length>1){
                this.removeScholarsGroup(this.selectedIds)
                this.$emit('removeItems')
                this.group.count=this.group.count-this.selectedIds.length+1
            }
        }
    }
}
</script>
<style scoped lang="scss">
.group__scholars-title {
    font-family: Alegreya Sans Bold;
    border-bottom: 1px dashed black;
    width: 35%;
    margin-bottom: 5px;
    padding: 3px 0;
}
.group__scholars-item{
    margin-bottom: 5px;
}
.remove__scholars{
    border-radius: 5px;
    margin-bottom: 17px;
    padding: 3px;
}
.group__scholars-list {
    margin-bottom: 10px;
}
</style>